.header {
    display: flex;
    flex-flow: row nowrap;
    height: 200px;
    margin: 16px 16px 0 16px;

    @media screen and (max-width: 600px) {
        display: flex;
        flex-flow: row nowrap;
    }
}
.avatar {
    display: block;
    width: 200px;
    height: 200px;
    background-color: #DBEAF7;
    border-radius: 0 50% 50% 50%;

    @media screen and (max-width: 600px) {
        border-radius: 50%;
        margin: 5px;
        align-self: center;
    }
}
.wrapper {
    padding: 10px 0 0 20px;
    width: 100%;
    height: 100%;
    display: flex;
    flex-flow: column nowrap;
    justify-content: space-between;
}
.welcome {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
}
.speech {
    left: -70px;
    display: inline-block;
    background-color: #1070CA;
    color: white;
    font-weight: bold;
    font-size: 20px;
    padding: 12px 15px;
    border-radius: 16px;
    position: relative;
    box-shadow: 0px 2px 18px 0px rgba(0, 0, 0, 0.6);

    &::after {
        position: absolute;
        bottom: -23px;
        content: '';
        display: block;
        width: 17px;
        height: 23px;
        background: url('../../assets/img/Speech.svg');
    }

    @media screen and (max-width: 600px) {
        display: none;
    }
}
.hamburgerMenu {
    // align-self: flex-start;
    width: 35px;
    height: 35px;
    background-color: red;
    position: relative;

    &::before {
        content: '';
        position: absolute;
        background-color: black;
        width: 3px;
        height: 20px;
        top: 0px;
        transform: rotate(90);
    }
}